import React from 'react';
import styles from './Header.module.css';
import personImg from '../../assets/header/person.svg'
import personBgImg from '../../assets/header/person-bg.svg'
import arrow from '../../assets/header/arrow.svg'
import { useNavigate } from "react-router-dom";

const Header = () => {
    const navigate = useNavigate();
    const navHandler = (action) => {
        switch (action) {
            case 'video':
                return navigate('/atomic/video')
            case 'audio':
                return navigate('/atomic/audio')
            case 'history':
                return navigate('/atomic/history')
            case 'logo':
                return navigate('/atomic/')
            default:
                throw new Error('  action, navHandler')
        }
    }

    return (
        <header className={styles.header}>
            <div className={styles.nav}>
                <h2 className={styles.logo} onClick={() => navHandler('logo')}></h2>
                <div className={styles.link}>
                    <button className={styles.button} onClick={() => navHandler('video')}></button>
                    <button className={styles.button} onClick={() => navHandler('audio')}></button>
                    <button className={styles.button} onClick={() => navHandler('history')}></button>
                </div>
            </div>
            <div className={styles.line}></div>
            <div className={styles.content}>
                <div className={styles.info}>
                    <img className={styles.arrow} src={arrow} alt=" "/>
                    <h2 className={styles.text}>
                         <span className={styles.span}></span>
                    </h2>
                    <button className={styles.use}></button>
                    {/*<img className={styles.arrowDown} src={arrowButton} alt=""/>*/}
                </div>
                <div className={styles.person}>
                    <img className={styles.personImg} src={personImg} alt=" "/>
                </div>
            </div>
        </header>
    );
};

export default Header;
